<template>
	<el-row :gutter="10">
		<!--  -->
		<el-col :span="4">
			<div class="data_box">
          <el-tabs type="border-card" stretch>
					<el-tab-pane label="">
          </el-tab-pane>
          </el-tabs>
				<el-button @click="addScent" type="success" plain size="mini" style="width: 100%;" icon="el-icon-plus"></el-button>
				<el-scrollbar height="calc(100vh - 126px);">
					<el-menu :default-active="active" class="el-menu-vertical-demo" active-text-color="#409eff" text-color="#000000" style="background: none;border: none;">
						<el-menu-item @click="getScentData(item)" :index="item.id.toString()" v-for="item in testScents" style="height: 35px;line-height: 35px;">
							<span class="el-icon-s-help"></span>
							<span>{{ item.name }}</span>
						</el-menu-item>
					</el-menu>
				</el-scrollbar>
			</div>
		</el-col>
		<!--  -->
		<el-col :span="20" style="padding: 0 20px;">
			<div class="scent-data-box" v-if="steps">
				<el-divider content-position="left"><b></b></el-divider>
				<el-row :gutter="5" style="margin-bottom: 20px;">
					<el-col :span="8">
						<el-input v-model="scentInfo.name" size="mini" maxlength="50" clearable>
							<template #prepend>
								
							</template>
							<template #append>
								<el-button @click="saveScent" plain icon="el-icon-circle-check" size="mini" type="success"></el-button>
							</template>
						</el-input>
					</el-col>
					<el-col :span="5" style="text-align: right;">
						<el-button @click="runScent" plain size="mini" type="success" icon="el-icon-s-promotion" ></el-button>
						<el-button @click="deleteScent" plain icon="el-icon-delete" size="mini" type="danger"></el-button>
					</el-col>
				</el-row>
				<el-divider content-position="left"><b></b></el-divider>
				<el-scrollbar height="calc(100vh - 210px)">
					<el-table :data="steps" row-key="id" style="width: 100%;margin-bottom: 10px;" :show-header="false" empty-text="">
						<el-table-column>
							<template #default="scope">
                 <div style="cursor: pointer;">
								<span class="el-icon-map-location" style="color: #409eff;font-weight: bold;font-size: 14px;">{{ '' +  (scope.$index+1)}}</span>
								<span style="font-weight: bold;font-size: 14px;margin-left: 10px">{{ scope.row.stepInfo.title }}</span>
                 </div>
							</template>
						</el-table-column>
						<el-table-column width="200px">
							<template #default="scope">
								<el-button @click="clickEditStep(scope.row.stepInfo.id)"  size="mini" type="primary" icon="el-icon-edit-outline"></el-button>
								<el-button @click="clickDelete(scope.row.id)" plain size="mini" type="danger" icon="el-icon-delete"></el-button>
							</template>
						</el-table-column>
					</el-table>
					<el-button @click="addStepDlg = true" plain icon="el-icon-plus" size="mini" type="success"></el-button>
				</el-scrollbar>
			</div>
      <div class="container" v-if="testScents.length===0">
      <el-empty>
          <el-button type="primary" @click="addScent"></el-button>
      </el-empty>
      </div>
		</el-col>
	</el-row>
	<!--  -->
	<el-drawer v-model="ResultDlg" :with-header="false" size="50%">
		<div style="padding:20px;">
			<el-descriptions title="" border :column="4">
				<el-descriptions-item label="">{{ runScentResult.all }}</el-descriptions-item>
				<el-descriptions-item label="">{{ runScentResult.success }}</el-descriptions-item>
				<el-descriptions-item label="">{{ runScentResult.fail }}</el-descriptions-item>
				<el-descriptions-item label="">{{ runScentResult.error }}</el-descriptions-item>
			</el-descriptions>
			<div style="height: 40px;line-height: 40px;"><b></b></div>
			<el-scrollbar height="calc(100vh - 180px)">
				<el-table :data="runScentResult.cases" style="width: 100%" empty-text="">
					<el-table-column type="expand">
						<template #default="props">
							<caseResult :result="props.row"></caseResult>
						</template>
					</el-table-column>
					<el-table-column label="" prop="name" />
					<el-table-column label="" prop="method">
            <template #default="props">
               <span v-if="props.row.type === 'api'">{{ props.row.method }}</span>
						</template>
          </el-table-column>
					<el-table-column label="" prop="status_cede">
            <template #default="props">
               <span v-if="props.row.type === 'api'">{{ props.row.status_cede }}</span>
						</template>
          </el-table-column>
					<el-table-column label="" prop="state" min-width="40px">
						<template #default="props">
							<span v-if="props.row.state == ''" style="color: #00AA7F;"></span>
							<span v-else style="color:#F56C6C">{{ props.row.state }}</span>
						</template>
					</el-table-column>
				</el-table>
			</el-scrollbar>
		</div>
	</el-drawer>
	<!--  -->
	<el-drawer v-model="editStepDlg" :with-header="false" size="40%"><EditCase :step="editStepId" style="padding: 0 10px;"></EditCase></el-drawer>
	<!--  -->
	<el-drawer v-model="addStepDlg" :with-header="false">
		<template #default>
			<el-tabs type="card" style="height: calc(100vh - 100px);">
				<el-tab-pane label="">
					<el-tree ref="treeM1" :data="interfaces1" show-checkbox :props="addTreeProps" node-key="id" default-expand-all highlight-current empty-text="">
						<template #default="{ node, data }">
							<span class="custom-tree-node">
								<div v-if="data.name">
									<b style="color:#409eff ;">
										<i class="el-icon-paperclip"></i>
										{{ data.name }}
									</b>
								</div>
								<div v-if="data.title">
									<b style="color:#409eff ;">
										<i class="el-icon-map-location"></i>
										
									</b>
									<span style="margin-left: 5px">{{ data.title }}</span>
								</div>
							</span>
						</template>
					</el-tree>
				</el-tab-pane>
				<el-tab-pane label="" name="second">
					<el-tree ref="treeM2" :data="interfaces2" show-checkbox :props="addTreeProps" node-key="id" default-expand-all :expand-on-click-node="false" empty-text="">
						<template #default="{ node, data }">
							<span class="custom-tree-node">
								<div v-if="data.name">
									<b style="color:#409eff ;">
										<i class="el-icon-paperclip"></i>
										{{ data.name }}
									</b>
								</div>
								<div v-if="data.title">
									<b style="color:#409eff ;">
										<i class="el-icon-map-location"></i>
										
									</b>
									<span style="margin-left: 5px">{{ data.title }}</span>
								</div>
							</span>
						</template>
					</el-tree>
				</el-tab-pane>
			</el-tabs>
			<div class="add-btns">
				<el-tooltip class="item" effect="dark" content="," placement="top-start">
					<el-button plain type="success" size="mini" @click="addToScent()"></el-button>
				</el-tooltip>
				<el-tooltip class="item" effect="dark" content="" placement="top-start">
					<el-button plain type="danger" size="mini" @click="addStepDlg = false"></el-button>
				</el-tooltip>
			</div>
		</template>
	</el-drawer>
</template>

<script>
/*

1()
2
3
4
5

*/
import { mapState, mapActions, mapGetters } from 'vuex';
import { ElNotification } from 'element-plus';
import { ElMessage, ElMessageBox } from 'element-plus';
import Sortable from 'sortablejs';
import EditCase from '../components/common/editCase.vue';
import caseResult from '../components/common/caseResult.vue';
export default {
	components: {
		EditCase,
		caseResult
	},
	data() {
		return {
			active: '1',
			// 
			scentInfo: null,
			// 
			steps: null,
			// ,
			addStepDlg: false,
			// 
			addTreeProps: {
				children: 'steps'
			},

			// 
			editStepDlg: false,
			//id
			editStepId: null,
			// 
			runScentResult: null,
			// 
			ResultDlg: false
		};
	},
	computed: {
		...mapState(['pro', 'testScents', 'envId']),
		...mapGetters(['interfaces1', 'interfaces2'])
	},
	methods: {
		...mapActions(['getAllScent', 'getAllInter']),
		// 
		async addScent() {
			const params = {
				name: 'New Scent',
				project: this.pro.id
			};
			const response = await this.$api.createTestScent(params);
			if (response.status === 201) {
				this.$message({
					type: 'success',
					message: '',
					duration: 1000
				});
				this.getAllScent();
			}
		},
		// 
		async deleteScent() {
			ElMessageBox.confirm('?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning'
			})
				.then(async () => {
					const response = await this.$api.delTestScent(this.scentInfo.id);
					if (response.status === 204) {
						ElMessage({
							type: 'success',
							message: '',
							duration: 1000
						});
            this.getAllScent();
					}
				})
				.catch(() => {
					ElMessage({
						type: 'info',
						message: '',
						duration: 1000
					});
				});
		},
		// 
		async saveScent() {
			const response = await this.$api.updateTestScent(this.scentInfo.id, this.scentInfo);
			if (response.status === 200) {
				this.$message({
					type: 'success',
					message: '',
					duration: 1000
				});
			}
			this.getAllScent()
		},
		// 
		async runScent() {
			if (this.envId) {
				const params = {
					env: this.envId,
					scene: this.scentInfo.id
				};
				this.$message({
					type: 'success',
					message: '!',
					duration: 1000
				});
				const response = await this.$api.runScene(this.scentInfo.id, params);
				console.log(response.data);
				if (response.status == 200) {
					// 
					this.runScentResult = response.data;
					this.ResultDlg = true;
				}
			} else {
				this.$message({
					type: 'warning',
					message: '!',
					duration: 1000
				});
			}
		},
		// 
		clickEditStep(stepId) {
			this.editStepDlg = true;
			this.editStepId = stepId;
		},
		// 
		getcheckedCase() {
			const Nodes1 = this.$refs.treeM1.getCheckedNodes();
			const Nodes2 = this.$refs.treeM2.getCheckedNodes();
			const Nodes = [...Nodes1, ...Nodes2];
			// 
			const result = Nodes.filter(function(item, index) {
				return item.title;
			});
			return result;
		},
		// 
		async getScentData(scent) {
			this.scentInfo = {...scent};
			const response = await this.$api.getScentDatas(scent.id);
			if (response.status === 200) {
				this.steps = response.data;
			}
		},
		// 
		async clickDelete(id) {
			const response = await this.$api.delScentData(id);
			if (response.status === 204) {
				this.$message({
					type: 'success',
					message: '',
					duration: 1000
				});
				this.getScentData(this.scentInfo);
			}
		},
		// 
		async addToScent() {
			this.checkedCase = this.getcheckedCase();
			let order_s = this.steps.length;
			for (let value of this.checkedCase) {
				let item = { ...value };
				order_s += 1;
				const response = await this.$api.addScentData({ step: item.id, scene: this.scentInfo.id, sort: order_s });
				if (response.status === 201) {
				  this.$message({
            type: 'success',
            message: '',
            duration: 1000
				})
				} else {
          this.$message({
            type: 'error',
            message: '',
            duration: 1000
				})

				}
			}
			this.addStepDlg = false;
			this.getScentData(this.scentInfo);
			this.getAllInter();
		},
		// 
		initSort() {
			// 
			const tbody = document.querySelector('.scent-data-box .el-table__body-wrapper tbody');
			const _this = this;
			Sortable.create(tbody, {
				onEnd({ newIndex, oldIndex }) {
					const currRow = _this.steps.splice(oldIndex, 1)[0];
					_this.steps.splice(newIndex, 0, currRow);
					// 
					_this.updateScentOrder();
				}
			});
		},
		// 
		async updateScentOrder() {
			// caseListorder
			this.steps.forEach((item, index, array) => {
				item.sort = index + 1;
			});
			// 
			const response = await this.$api.updateScentDataOrder(this.steps);
			if (response.status === 200) {
				this.$message({
					type: 'success',
					message: '',
					duration: 500
				});
			}
		}
	},
	async mounted() {
		if (this.testScents.length > 0) {
			// 
			this.active = this.testScents[0].id.toString();
			this.scentInfo = { ...this.testScents[0] };
			await this.getScentData(this.scentInfo);
			// 
      try {
        const tbody = document.querySelector('.scent-data-box .el-table__body-wrapper tbody');
        console.log(tbody); //  tbody
        this.initSort(); // 
      } catch (error) {
        // 
        console.error('', error);
        // 
      }
		}
	}
};
</script>

<style scoped>
.data_box {
	box-shadow: 0 0 5px #dcdfe6;
}
.add-btns {
	text-align: center;
	margin-top: 50px;
	height: 150px;
}
.title {
	text-align: center;
	font: bold 16px/30px 'microsoft yahei';
	height: 30px;
}
.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70vh;
  }
</style>
